* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
  

/* TO DO APP STYLE */
.headName {
  border: 10px groove lightskyblue;
  background-color: lightgray;
  width: 60%;
  margin: 0 auto;
  margin-top: 30px;
}
.toDoApp {
  border: 10px groove lightskyblue;
  background-color: lightgray;
  color: black;
  width: 60%;
  margin: 0 auto;
  margin-top: 20px;
}
span  {
  color: red
}
.inputTask {
  width: 100%;
}
.ToDo {
  width: 100%;
  margin: 0 auto;
}
.taskForm {
  width: 80%;
  margin: 0 auto;
}
.inputName {
  width: 75%;
}
.addTaskBtn {
  padding: 2px;
  border-radius: 5px;
  background-color: yellowgreen;
}
.showBtn {
  padding: 5px;
  margin: 10px;
  border-radius: 5px;
  background-color: yellowgreen;
}
.hideBtn {
  padding: 3px;
  margin: 5px;
  color: red;
  border-radius: 5px;
  border: black 1px solid;
}
.newTask {
  display: flex;
  justify-content: space-around;
}
h3 {
  color: red
}
.taskTable {
  margin: 0 auto;
  margin-top: 10px;
}
h2 {
  color: blue
  }



/*  */
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
